import Header from "../../componentes/header";
import Footer from "../../componentes/footer";
import PanelContribuyente from "../../componentes/panelContribuyente";
import Notificacion from "../../componentes/notificacion";
import { useState,useEffect } from "react";
import { useSearchParams } from 'react-router-dom'
import PanelPlanPago from "../../componentes/panelPlanPago";
import TablaResumenPlanPago from "../../componentes/tablaResumenPlanPago";

function PlanPago() {
    const [notificacionVisible, setNotificacionVisible] = useState(false);
    const [textoNotificacion, setTextoNotificacion] = useState("");
    const [nombreImpuesto, setNombreImpuesto] = useState("");
    const [codigoRecibo, setCodigoRecibo] = useState("");
    const[recarga, setRecarga] = useState(false);
    const [searchParams, setSearchParams] = useSearchParams();
    const [esAutomotor, setEsAutomotor] = useState(false);

    useEffect(() => {
        setCodigoRecibo(searchParams.get('Codigo'));
    }, []);
    
    return (
        <div>
            <Header  hideButton={false} hideReturnMenu={false} certificadoPlanPago={true} />
            <div style={{marginTop:64,marginBottom:84}}>
                <div style={{paddingTop: 20, paddingBottom:20}}>
                    <PanelContribuyente 
                        generarMensaje={generarMensaje}
                        generarMensajeExito={generarMensajeExito}
                    />
                    <PanelPlanPago codigoImpuesto={codigoRecibo}/>
                    <Notificacion 
                        mensaje={textoNotificacion} 
                        visible={notificacionVisible} 
                        funcionAbrir = {abrirNotificacion} 
                        funcionCerrar = {cerrarNotificacion}
                    />
                </div>
                <TablaResumenPlanPago setRecarga={setRecarga} recarga={recarga} codigoRecibo={codigoRecibo} />
            </div>
          <Footer sugerenciaActiva={true} />
        </div>
    );
    function generarMensaje(mensaje){
        setTextoNotificacion(mensaje)
        setNotificacionVisible(true)
      }
    function generarMensajeExito(mensaje){
        setTextoNotificacion(mensaje)
        setNotificacionVisible(true)
    }
    function abrirNotificacion(){
        setNotificacionVisible(true)
    }
    function cerrarNotificacion(){
        setNotificacionVisible(false)
    }
}

export default PlanPago;