import '../styles/HeaderStyle.css';
import '../styles/FooterStyle.css';
import '../styles/ContainerStyle.css';
import '../styles/SesionStyles.css';
import '../styles/ModalStyle.css'
import Header from "../componentes/header"
import Footer from "../componentes/footer"
import rutas from '../configuracion/Configuracion';
import { useState, useEffect,useRef } from "react";
import {
    TextField,
    Button,
    InputAdornment,
    IconButton
} from "@mui/material";
import ModalCargando from '../componentes/modalCargando';
import Modal from '../componentes/modal';
import { useSearchParams } from 'react-router-dom'
import IconoAlert from '../componentes/iconos/iconoAlert';
import IconoCorrecto from '../componentes/iconos/iconoCorrecto';
import IconoEspera from '../componentes/iconos/iconoEspera';

function PagoQR() {
    const tableStyle = {
        width: '300px',
        borderCollapse: 'collapse',
        marginTop: '30px',
        backgroundColor: '#f2f2f2',
        boxShadow: '4px 4px 8px rgba(0, 0, 0, 0.2)',
    };
    const tdStyle = {padding: '8px',border: '1px solid #ddd',fontSize: '14px',};
    const nombreDatoStyle = {textAlign: 'left',fontWeight: 'bold',};
    const valorTextoStyle = {textAlign: 'left',};
    const valorNumeroStyle = {textAlign: 'right',};
    const cargado = useRef(false);
    const [cargando, setCargando] = useState(true)
    const [mostrarMensajeError, setMostrarMensajeError] = useState(false);
    const [mostrarMensajeAcreditando, setMostrarMensajeAcreditando] = useState(false);
    const [MensajeError, setMensajeError] = useState("");
    const [MensajeEstadoQR, setMensajeEstadoQR] = useState("");
    const [searchParams, setSearchParams] = useSearchParams();
    const [estadoPediente, setEstadoPediente] = useState(false);
    const [estadoAcreditado, setEstadoAcreditado] = useState(false);
    const [estadoCancelado, setEstadoCancelado] = useState(false);
    

    ///Datos publicos de la respuesta
    const [documentoPublico, setDocumentoPublico] = useState("")
    const [ConceptoPublico, setConceptoPublico] = useState("")
    const [TotalPublico, setTotalPublico] = useState("")
    const [TipoPublico, setTipoPublico] = useState("")

    ///Datos del form
    const [CallbackSuccess, setCallbackSuccess] = useState("")
    const [CallbackCancel, setCallbackCancel] = useState("")
    const [Total, setTotal] = useState("")
    const [SucursalComercio, setSucursalComercio] = useState("")
    const [Comercio, setComercio] = useState("")
    const [Concepto, setConcepto] = useState("")
    const [Informacion, setInformacion] = useState("")
    const [TransaccionComercioId, setTransaccionComercioId] = useState("")
    useEffect(() => {
        const form = document.querySelector('form');
    
        const handleSubmit = (event) => {
          console.log('Formulario enviado correctamente');
          setCargando(true)
          setTimeout(() => {
            validarEstadoQR();
            setCargando(false)
          }, 15000);
        };
    
        form.addEventListener('submit', handleSubmit);
        return () => {
          form.removeEventListener('submit', handleSubmit);
        };
      }, []);
    useEffect(() => {
        let dataCabecera = searchParams.get('data');
        
        if (!cargado.current && dataCabecera) {
            cargado.current = true;
            async function obtenerDatos() {
                try {
                    setCargando(true);
                    let url = rutas.API + rutas.GenerarPagoQR + dataCabecera;
                    alert(url)
                    const response = await fetch(url,{referrerPolicy:'unsafe-url'});
                    const data = await response.json();
                    if (data.Exito && data.Datos.Respuesta) {
                        if (data.Datos.Datos.existe) {
                            //el caso de exito pero con respuesta = true quiere decir que mi operacion fue correcta
                            //pero la operacion primaria que es el QR fallo, esto implica que ya existe una transaccion pre-existente para el codigo
                            validarEstadoQR();
                        }else{
                            setDocumentoPublico(data.Datos.Datos.documento);
                            setTotalPublico(data.Datos.Datos.monto);
                            setConceptoPublico(data.Datos.Datos.referencia);
                            setTipoPublico(data.Datos.Datos.tipo);

                            setCallbackSuccess(data.Datos.Datos.urlexito);
                            setCallbackCancel(data.Datos.Datos.urlerror);
                            setTotal(data.Datos.Datos.montoProducto);
                            setSucursalComercio(data.Datos.Datos.sucursal);
                            setComercio(data.Datos.Datos.comercio);
                            setConcepto(data.Datos.Datos.producto);
                            setInformacion(data.Datos.Datos.info);
                            setTransaccionComercioId(data.Datos.Datos.transacid);
                            setCargando(false);
                        }
                    }else{
                        setMensajeError(data.Error)
                        setMostrarMensajeError(true)
                        setCargando(false);
                    }
                    
                } catch (error) {
                    console.error(error);
                }
            }
            obtenerDatos();
            
        }
    }, [searchParams]);
    useEffect(() => {
        if(estadoPediente === true){
            const intervalId = setInterval(() => {
                loop();
            }, 10000);
          
            return () => clearInterval(intervalId);
        }
    }, [estadoPediente]);
    const loop = async (paramCabecera) => {
        console.log("Actualizando Estado Pendiente...")
        validarEstadoQR();
    };
    const validarEstadoQR = async () =>{
        let dataCabecera = searchParams.get('data');
        
        if(dataCabecera){
            try {
                const response = await fetch(rutas.API + rutas.ValidarEstadoPagoQR + dataCabecera);
                const data = await response.json();
                if(data.Exito && data.Datos.Respuesta && data.Datos.Datos.existe){
                    setEstadoAcreditado(data.Datos.Datos.acreditado)
                    setEstadoCancelado(data.Datos.Datos.cancelado)
                    setEstadoPediente(data.Datos.Datos.pendiente)
                    setMostrarMensajeAcreditando(true)
                    setMensajeEstadoQR(data.Datos.Datos.mensaje)
                }else{
                    setMostrarMensajeError(true);
                    setMensajeError("Ocurrio un error el la carga del pago, por favor recargue la pagina.")
                }
            } catch (error) {
                setMostrarMensajeError(true);
                setMensajeError("Ocurrio un error el la carga del pago, por favor recargue la pagina.")
            }
        }
    }  
    const ocultarCargando = () =>{
        setCargando(false)
    }
    function cerrarMensaje(){
    }
    return(
        <div>
            <Header/>
            <ModalCargando estadoModal={cargando} cerrarModal={ocultarCargando}></ModalCargando> 
            <Modal isOpen={mostrarMensajeError} closeModal={cerrarMensaje} style={{marginTop:200}}>
                <div className="modal-background" visible>
                    <div className="modal-container" style={{minWidth:600}}  >
                        <IconoAlert ></IconoAlert>
                        
                        <h2>{MensajeError}</h2>
                    </div>
                </div>
            </Modal>
            {/*Modal de pago ya acreditado*/}
            <Modal isOpen={mostrarMensajeAcreditando} closeModal={cerrarMensaje} style={{marginTop:200}}>
                <div className="modal-background" visible>
                    <div className="modal-container" style={{minWidth:600}}  >
                        { estadoCancelado && <IconoAlert ></IconoAlert>}
                        { estadoAcreditado && <IconoCorrecto ></IconoCorrecto>}
                        { estadoPediente && <IconoEspera ></IconoEspera>}
                        <h2>{MensajeEstadoQR}</h2>
                    </div>
                </div>
            </Modal>
            <div className="form-container" style={{marginTop:80, fontFamily: "Roboto,sans-serif", width:'300px'}}>
                <img className="logo-login" 
                    alt="" 
                    height="100" 
                    width="150" 
                    src="/imagenes/LogoHeader.png" 
                    style={{ marginTop:10}}
                />
                <div className="input-login" style={{alignItems: "center", width:'300px'}} >
                    <li>
                        <div>
                            <div style={{
                                boxShadow: '4px 4px 8px rgba(0, 0, 0, 0.1)',
                            }}>
                                <h1 style={{textAlign: 'center',color:'#1a1a1a',padding: 6}} >Pago QR</h1>
                            </div>
                            
                            <table style={tableStyle}>
                                <tbody>
                                    <tr>
                                    <td style={{ ...tdStyle, ...nombreDatoStyle }}>Documento</td>
                                    <td style={{ ...tdStyle, ...valorTextoStyle }}>{documentoPublico}</td>
                                    </tr>
                                    <tr>
                                    <td style={{ ...tdStyle, ...nombreDatoStyle }}>Tipo de Documento</td>
                                    <td style={{ ...tdStyle, ...valorTextoStyle }}>{TipoPublico}</td>
                                    </tr>
                                    <tr>
                                    <td style={{ ...tdStyle, ...nombreDatoStyle }}>Concepto</td>
                                    <td style={{ ...tdStyle, ...valorTextoStyle }}>{ConceptoPublico}</td>
                                    </tr>
                                    <tr>
                                    <td style={{ ...tdStyle, ...nombreDatoStyle }}>Monto</td>
                                    <td style={{ ...tdStyle, ...valorNumeroStyle }}>{TotalPublico}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div style={{marginTop:20}}>
                            <form target="_blank" method='post' action={rutas.EndPointPagosMacro}>
                                <input type='hidden' name='CallbackSuccess' value={CallbackSuccess}/>
                                <input type='hidden' name='CallbackCancel' value={CallbackCancel}/>
                                <input type='hidden' name='Monto' value={Total}/>
                                <input type='hidden' name='SucursalComercio' value={SucursalComercio}/>
                                <input type='hidden' name='Comercio' value={Comercio}/>
                                <input type='hidden' name='Producto[0]' value={Concepto}/>
                                <input type='hidden' name='Informacion' value={Informacion}/>
                                <input type='hidden' name='TransaccionComercioId' value={TransaccionComercioId}/>
                                <Button 
                                    style={{display: "block", margin: "auto"}}
                                    variant="contained"  
                                type='submit'>
                                    Pagar
                                </Button>
                            </form>
                        </div>
                    </li>
                </div>
            </div>
            <Footer/>
        </div>
    );
}
export default PagoQR 